@import url(https://fonts.googleapis.com/css?family=Do+Hyeon|Nanum+Myeongjo:400,700|Roboto:400,700);
html {
  position: relative;
  min-height: 100%; }

body header {
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: "Do Hyeon", sans-serif; }

body footer {
  padding-top: 60px;
  font-family: "Do Hyeon", sans-serif; }

body .container {
  font-family: "Nanum Myeongjo", sans-serif;
  margin-bottom: 60px;
  /* Margin bottom by footer height */ }

body .card {
  margin: 3px;
  font-family: "Nanum Myeongjo", sans-serif; }

.Main .card {
  position: relative;
  margin: 30px 0;
  padding: 30px 0;
  color: white;
  /* animation */
  -webkit-animation-name: main-card-animation;
          animation-name: main-card-animation;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; }

.Main .service {
  font-size: 2.5rem; }

@-webkit-keyframes main-card-animation {
  0% {
    background-color: white; }
  25% {
    background-color: #16a085; }
  100% {
    background-color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15); } }

@keyframes main-card-animation {
  0% {
    background-color: white; }
  25% {
    background-color: #16a085; }
  100% {
    background-color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15); } }

.AboutTeam .avatar {
  vertical-align: middle;
  width: 320px;
  height: 320px;
  border-radius: 50%; }

.AboutTeam .avatar.size-1 {
  width: 40px;
  height: 40px; }

.AboutTeam .avatar.size-2 {
  width: 70px;
  height: 70px; }

.AboutTeam .avatar.size-3 {
  width: 90px;
  height: 90px; }

.AboutTeam .avatar.size-4 {
  width: 110px;
  height: 110px; }

@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

/* 레이아웃 전반 스타일*/
.title {
  text-align: center;
  font-weight: bold;
  font-size: 20pt; }

.container {
  padding: 10px 10px 10px 10px; }

.panel-borderless {
  border: 0;
  box-shadow: none; }

.pointer {
  cursor: pointer; }

.name {
  font-size: 1.5em;
  font-weight: 400; }

/* 사진의 크기 제한 */
.photoWidth {
  max-width: 80px;
  min-width: 80px; }

/* 모달 박스를 위한 스타일*/
.modal {
  display: block;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.4); }

.form {
  background-color: white;
  margin: 80px auto;
  max-width: 400px;
  min-width: 200px;
  font: 13px "verdana";
  padding: 10px 10px 10px 10px; }

.loading {
  margin: 200px auto; }

.form div {
  padding: 0;
  display: block;
  margin: 5px 0 5px 0; }

.form label {
  text-align: left;
  margin: 0 0 3px 0;
  padding: 0px;
  display: block;
  font-weight: bold; }

.form input,
textarea,
select {
  box-sizing: border-box;
  border: 1px solid #bebebe;
  padding: 7px;
  margin: 0px;
  outline: none; }

.form .long {
  width: 100%; }

.form .button {
  background: #2b798d;
  padding: 8px 15px 8px 15px;
  border: none;
  color: #fff; }

.form .button:hover {
  background: #4691a4; }

.form .heading {
  background: #33a17f;
  font-weight: 300;
  text-align: left;
  padding: 20px;
  color: #fff;
  margin: 5px 0 30px 0;
  padding: 10px;
  min-width: 200px;
  max-width: 400px; }

/* 둥근 버튼을 위한 스타일*/
.btn-circle {
  width: 30px;
  height: 30px;
  padding: 6px 0px;
  border-radius: 15px;
  text-align: center;
  font-size: 12px;
  line-height: 1.42857; }

.SearchForm .input-group {
  height: 3rem;
  margin-bottom: 1rem; }
  .SearchForm .input-group .input {
    width: 100%;
    padding: 0 1rem;
    border: 2px #3DC829 solid; }

.Header {
  padding: 0 20px; }

.Brand {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; }
  .Brand .icon {
    width: 45px;
    height: 45px;
    margin: 5px;
    border-radius: 20%;
    background-color: #fffa65;
    border: 2px solid #05c46b; }
  .Brand .title {
    color: #576574;
    font-size: 2.3rem;
    margin: 5px; }

.Language {
  padding: 0 15px; }

